<template>
  <div id="ModulesRegister">
    <button
      @click="add()"
      style="background-color: #1d425e; color: white"
      class="btn mb-3"
    >
      AÃ±adir MÃ³dulo
    </button>
    <div>
      <b-modal scrollable centered v-model="show" title="AÃ±adir mÃ³dulo">
        <b-container fluid>
          <!--Contenido modal-->
          <form>
            <AlertError ref="error" />
            <div class="form-group">
              <label for="validationServer01">Nombre</label>
              <b-form-input
                type="text"
                class="form-control"
                placeholder="Ingrese nombre"
                v-model="data.name"
              />
            </div>
            <div class="form-group">
              <label for="exampleInputPassword1"
                >Orden
                <b-icon-info-circle-fill
                  color="rgb(119, 35, 45)"
                  v-b-popover.hover.right="
                    'Ingresar un nÃºmero de orden no repetido en los mÃ³dulos ya creados.'
                  "
              /></label>
              <b-input-group prepend="MÃ³dulo" class="mb-2 mr-sm-2 mb-sm-0">
                <b-form-input
                  type="number"
                  class="form-control"
                  v-model="data.order"
                  placeholder="Ingrese nÃºmero de orden"
                />
              </b-input-group>
            </div>
            <div class="form-group">
              <label for="exampleInputPassword1">DescripciÃ³n</label>
              <b-form-textarea
                placeholder="Ingrese descripciÃ³n"
                v-model="data.description"
              />
            </div>
          </form>
        </b-container>

        <template #modal-footer>
          <div class="w-100">
            <button
              type="button"
              @click="saveAddItem(data)"
              style="background-color: #1d425e; border: none; color: white"
              class="btn form-control my-2"
            >
              Enviar
            </button>
            <button
              type="button"
              @click="exit()"
              style="
                background-color: rgb(119, 35, 45);
                border: none;
                color: white;
              "
              class="btn form-control"
            >
              Cancelar
            </button>
          </div>
        </template>
      </b-modal>
    </div>
  </div>
</template>

<script>
import AlertError from "../../components/AlertError.vue";
export default {
  name: "ModulesRegister",
  components: { AlertError },
  props: {
    idIdentifier: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      show: false,
      data: {},
    };
  },
  methods: {
    exit() {
      this.show = false;
    },
    add() {
      (this.data = {
        name: "",
        order: null,
        description: "",
      }),
        (this.show = true);
    },
    async saveAddItem() {
      await this.$axios
        .post(
          "/configuration/program/" + this.idIdentifier + "/modulus-register",
          this.data,
          {
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        )
        .then((res) => {
          this.show = false;
          this.$message.success(res.data.content);
          this.$parent.initialize();
        })
        .catch((e) => {
          console.log(e);
          this.$refs.error.showAlert(e.response.data.content);
        });
    },
  },
};
</script>
<style>
.modal-content {
  background-color: rgb(243, 243, 243);
}
</style>